:root {
  --background-color: #ffffff;
  --title-color: #000;
  --dwn-bg-color: #fff6f4;
  --contact-bg-color: #f9f9f9;
  --body-text-color: #64626a;
  --light-body-text: #b3b1b7;
  --theme-color: #ff6846;
  --primary-highlighter-color: #ffdea7;
  --secondary-highlighter-color: #c2eed8;
  --tertiary-highlighter-color: #c0eef5;
  --enterprise-color: #f5f6f9;
  --theme-base-font: Libre Baskerville, serif;
  --body-base-font: Inter, sans-serif;
}

html[data-theme="light"] {
  --background-color: #fff;
  --title-color: #000;
  --dwn-bg-color: #fff6f4;
