.main-menu .dropdown-toggle {
  transition: .4s;
  align-items: center;
}

.main-menu .dropdown-toggle:after {
  margin-left: 10px;
  /* margin-right: 4%; */
  /* margin-top: -3px; */
  position: relative;
  top: 1px;
}

@media (min-width: 20em) and (max-width: 62em) {
  .main-menu .dropdown-toggle:after {
    margin-left: auto !important;
    margin-right: 5% !important;
    top: 0px;
  }
}

.main-menu .nav-item.dropdown .dropdown-toggle::after {
  margin-left: auto;
}

@media (min-width: 85.375em) {
  .main-menu .nav-item.dropdown:hover > .dropdown-toggle:after {
    transform: rotate(0deg);
  }
}

.main-menu .drop-menu-item.dropdown > .dropdown-toggle {
  position: relative;
  display: flex;
  align-items: center;
}

.main-menu .drop-menu-item.dropdown > .dropdown-toggle:after {
  transform: rotate(-90deg);
  -webkit-transition: .4s;
  transition: .4s;
}

@media (min-width: 85.375em) {
  .main-menu .drop-menu-item.dropdown:hover > .dropdown-toggle:after {
    transform: rotate(0deg);
  }
}

.main-menu .dropdown.show > .dropdown-toggle:after, .main-menu .dropdown ~ .show > .dropdown-toggle:after {
  transform: rotate(0deg);
}

@media (min-width: 85.375em) {
  .menu-dropdown {
    top: 110%;
    position: absolute;
    min-width: 227px;
    max-width: 227px;
    box-shadow: 0 52px 54px rgba(65, 62, 101, 0.3);
    border-radius: 8px;
    border: 1px solid #E5E5E5;
    background-color: #FFFFFF;
    padding: 15px 0px;
    z-index: 99;
    opacity: 0;
    transition: opacity .4s,top .4s;
    pointer-events: none;
    left: -90%;
    border-radius: 0 0 10px 10px;
    border: 1px solid #eae9f2;
    background-color: #ffffff;
    display: block;
    border-top: 3px solid #5454d4;
  }
}

.menu-dropdown > .drop-menu-item {
  color: #19191b;
  font-size: 13px;
  font-weight: 300;
  letter-spacing: -0.5px;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 10px;
  padding-bottom: 10px;
}

.menu-dropdown > .drop-menu-item:hover > a {
  color: #5454d4;
}

.menu-dropdown > .drop-menu-item.dropdown {
  position: relative;
}

@media (min-width: 85.375em) {
  .menu-dropdown > .drop-menu-item.dropdown:hover > .menu-dropdown {
    top: 10px;
    opacity: 1;
    pointer-events: visible;
    transform: translateX(-100%);
  }
}

