/*==== Footer Styles -------*/
.toggle-contanier-switch_theme {
  position: absolute;
  top: 49px;
  left: 13px;
  width: 70px;
  transform: scale(0.6, 0.4);
}

#time {
  position: absolute;
  right: 100vw;
}

#time + label {
  --i: 0;
  --j: calc(1 - var(--i));
  display: grid;
  grid-gap: 0.5em 0.25em;
  overflow: hidden;
  padding: 0.5em;
  height: 2.5em;
  border-radius: 1.75em;
  background: hsl(23, 93%, calc(var(--j) * 50%));
  color: transparent;
  user-select: none;
  transition: 0.3s;
  cursor: pointer;
}

#time + label:before, #time + label:after {
  width: 1.5em;
  height: 1.5em;
  transition: inherit;
  content: "";
}

#time + label:before {
  transform-origin: 20% 20%;
  transform: translate(calc(var(--i) * (100% + 0.25em))) scale(calc(1 - var(--i) * 1));
  background: #e8e8de;
  border-radius: 50px;
}

#time + label:after {
  grid-column: 2;
  border-radius: 50%;
  transform: translatey(calc(var(--i) * (-100% - 0.5em)));
  background: radial-gradient(circle at 23% 35%, transparent 52%, #fff 43%);
}

#time:checked + label {
  --i: 1;
}
